import { useState } from 'react'

import { Box, Modal, Typography, styled } from '@mui/material'

import NewPasswordForm from 'src/components/NewPasswordForm'

import { useChangePassword } from 'src/hooks/api/authHooks'

interface PasswordModalProps {
  open: boolean
  setOpen: (open: boolean) => void
  onClose: (event: React.MouseEvent<HTMLElement>, reason: 'backdropClick' | 'escapeKeyDown') => void
}

const ModalBox = styled(Box)(
  ({ theme }) => `
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 360px;
    width: 90%;
    background-color: #fff;
    box-shadow: 24;
    padding: ${theme.spacing(4)};
  `
)

function PasswordModal({ open, setOpen, onClose }: PasswordModalProps) {
  const [values, setValues] = useState({ currentPassword: null, newPassword: null })
  const changePasswordMutation = useChangePassword(values)

  const handleChange: React.ChangeEventHandler<HTMLInputElement> = event => {
    if (event.target.type === 'password') {
      const newPassword = document.getElementById('new-password') as HTMLInputElement
      const confirmPassword = document.getElementById('confirm-password') as HTMLInputElement
      if (newPassword.value !== confirmPassword.value) confirmPassword.setCustomValidity("Passwords don't match")
      else confirmPassword.setCustomValidity('')
    }
    if (event.target.id !== 'confirm-password') {
      const newValues = {
        ...values,
        [event.target.name]: event.target.value
      }
      setValues(newValues)
    }
  }
  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async e => {
    e.preventDefault()

    changePasswordMutation.mutateAsync()
    setOpen(false)
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      aria-labelledby="password-modal-title"
      aria-describedby="password-modal-description"
    >
      <ModalBox>
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          비밀번호 수정
        </Typography>
        <NewPasswordForm
          isForgotPassword={false}
          handleChange={handleChange}
          onClickSubmit={handleSubmit}
          onClickCancel={() => setOpen(false)}
        />
      </ModalBox>
    </Modal>
  )
}

export default PasswordModal
